.section{
    display: flex;
}

.firstSection {
    width: 70%;
    height: 100vh;
    
}

.arenaImg{
    margin-top: 1.6rem;
    margin-left: 1.3rem;
    width: 10vw;
}

.outerDiv{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.loginForm{
    box-shadow: 1px 2px 10px 2px rgb(213, 213, 220);
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    padding: 2.5rem 4rem;
    width: 30%;
    margin-top: 3rem;
}

.loginForm p{
    font-size: 16pt;
    font-weight: 300;
}

.loginForm input{
    padding: 0.65rem 0.75rem;
    border: 1px solid rgb(197,197,205);
    border-radius: 3px;
    width: 100%;
    font-size: 16px;
    margin-top: 1.6rem;
}

.loginForm input::placeholder{
    color: rgb(197,197,205);
}

#link1{
    margin-top: 1.2rem;
}

#link2{
    margin-top: 0.6rem;
}

.link{
    color: rgb(197,197,205);
    cursor: pointer;
    line-height: 1.5;
    font-weight: 500;
}

.loginForm span{
    color: rgb(197,197,205);
    text-decoration: underline;
    text-decoration-style: dotted;
}

.loginForm button{
    width: 10.5vw;
    margin-top: 2.5rem;
}

.secondSection {
    width: 25.5%;
    height: 100vh;
    background: linear-gradient(to bottom, white, #b3cbfc);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px 40px;
}

.dpImg{
    border-radius: 100%;
    margin-top: 5.6rem;
    width: 150px;
}

.quoteImg{
    width: 40px;
    margin-right: 27.5rem;
}

.quoteDiv p{
   margin-top: 18px;
   font-style: italic;
   line-height: 1.5;
}

#ptag{
    font-style: normal;
    font-size: 17.5px;
    margin-top: 3.2rem;
}

.bold{
    font-weight: 600;
}

.quoteImg2{
    width: 40px;
    margin-left: 25.5rem;
    margin-top: 1rem;
}