.header{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    gap: 20px;
    margin-right: 40px;
    margin-top: 20px;
}
.log-btn{
    width: 150px;
    border-radius: 30px;
    background-color: white;
    color: rgb(36,116,204);
    border: 1px solid rgb(36,116,204);
    font-size: 18px;
    font-weight: bold;
}

button{
    width: 150px;
    height: 50px;
    border-radius: 30px;
    background: rgb(36,116,204);
    border: 1px solid rgb(36,116,204);
    color: white;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
}

.inpDiv{
   display: flex;
   flex-direction: column;
   align-items: center;
   margin-top: 136.5px;
}

.inp{
    margin-top: 53px;
    margin-left: 25px;
    background-color: rgb(247,247,251);
    border: 1px solid rgb(222, 222, 224);
    width: 34.5vw;
    height: 3vh;
    border-radius: 25px;
    padding: 10px 30px;
    outline: none;
}

.logo{
    position: relative;
    left: -50px; 
    top: 5px;
    width: 25px;
    cursor: pointer;
}

.footer{
    display: flex;
    align-content: flex-end;
    margin-top: 16.8rem;
    justify-content: space-between;
    padding: 0px 10px;
}

.footerList{
    margin-top: 7px;
    display: flex;
    list-style: none;
    gap: 4px;
}

.footerList li{
    color: #007bff;
    font-size: 13px;
    font-weight: 500;
    
}

.footerList li:hover{
    cursor: pointer;
    color: rgb(36,116,204);
    text-decoration: underline;
}

span{
    color: black;
}

.first{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.first p{
    margin-left: 36px;
    font-weight: 400;
}

.second{
    display: flex;
    gap: 25px;
    align-items: center;
    padding: 0px 60px;
}
